import React,{ useState } from 'react'
import { Card, Form, Modal, Row, Col, Button } from 'antd';
import Flex from 'components/shared-components/Flex'
import { Typography } from "antd";
import List from './list';
import moment from 'moment';
import { DatePicker } from 'antd';
import { CloudDownloadOutlined } from '@ant-design/icons';

const { RangePicker } = DatePicker;
const { Title } = Typography;
let stockTotal = 0;
let reportData = [];
const Report = ({systemData}) => {
	const [form] = Form.useForm();
	const [roadTalls, setRoadTalls] = useState([]);
	const [orderTrips, setOrderTrips] = useState([]);
	const [orderGarage, setOrderGarage] = useState([]);
	const [orderOutsourced, setOrderOutsourced] = useState([]);
	const [orderFuel, setOrderFuel] = useState([]);
	const [allOrders, setAllOrders] = useState([]);
	const [allTripsLoading, setAllTripsLoading] = useState(true);
	const [allTrips, setAllTrips] = useState({}); 
	const [externalTrips, setExternalTrips] = useState([]);
	const [fDate, setFdate] = useState('');
	const [tDate, setTdate] = useState('');
	const [visible, setVisible] = useState(false);
	const [loaded, setLoaded] = useState(false);
	let { orders, ordersLoading, trips, tripsLoading, avatarColors, allDestinationsLoading, allDestinations, allCurrencyLoading, allCurrency, trucks, trucksLoading, allRentedTrucks, garages, rentedTrucks, rentedTrucksLoading, currencies, currenciesLoading, outsourcing, outsourcingLoading, allTrucks, roadTall, allBorders, fuels, allCountries, allCountriesLoading, destinations, destinationsLoading, allTripsLoading:tripDataLoading, allTrips:tripData, rounder } = systemData;
	// records = recordsLoading ? [] : records.docs.filter(x => x.data().sales === true);
	
	const rangeConfig = {
		rules: [{ type: 'array', required: true, message: 'Please select time!' }],
	  };
	  
	// const setTableData = (index, subscriber,length) => {
	// 	reportData = index === 0 ? [] : reportData ;  
	// 	stockTotal = index === 0 ? 0 : stockTotal;  
	// 	let {quantity,purchasedType,saleId} = subscriber.data();
	// 	let display = (quantity + ' x ' + (purchasedType === '0' ? ' Pcs' : (purchasedType === '1' ? ' Cts' : ' ' + shortTypes[parseInt(purchasedType)-1])));
	// 	stockTotal += parseInt(subscriber.data().total)
	// 	let newRow = {
	// 		dates: saleId,
	// 		product:subscriber.data().product,
	// 		quantity: display,
	// 		amount: subscriber.data().total,
	// 		solde:stockTotal,
	// 	}
	// 	reportData.push(newRow);
	// 	if(parseInt(index+1) === length){
	// 		reportData.push({dates: '',product:'',quantity: '',amount: 'Total',solde:stockTotal});
	// 	}
	// };

	  const handleCancel = () => {
		setVisible(false)
		setLoaded(false);
	};

	const disabledDate = (current) => {
		return current && current > moment().endOf("day");
	} 

	  const handleOk = async () => {
		try {
			const fieldsValue = await form.validateFields();
			let rangeValue = fieldsValue['range-picker'];
			let fdate = new Date(rangeValue[0].format('YYYY-MM-DD')).setHours(0,0,0,0)
			let tdate = new Date(rangeValue[1].format('YYYY-MM-DD')).setHours(23,59,59,999)
			// let toMap = records.filter(x => x.data().date >= fdate &&  x.data().date <= tdate);
			let trps = trips.docs.filter(x => x.data()._id >= fdate &&  x.data()._id <= tdate);
			let outs = outsourcing.docs.filter(x => x.data()._id >= fdate &&  x.data()._id <= tdate);
			let talls = roadTall.docs.filter(x => x.data()._id >= fdate &&  x.data()._id <= tdate);
			let fls = fuels.docs.filter(x => x.data()._id >= fdate &&  x.data()._id <= tdate);
			let grg = garages.docs.filter(x => x.data()._id >= fdate &&  x.data()._id <= tdate);
			let ords = orders.docs.filter(x => x.data().date >= fdate &&  x.data().date <= tdate);

			if(!tripsLoading && !ordersLoading && !outsourcingLoading){
				let allRecords = [];
				
				orders.docs.filter(x => x.data().date >= fdate &&  x.data().date <= tdate).map((doc)=>{
					allRecords = {
						...allRecords,
						[doc.data()._id] : 0,
					}
				})
		
				outsourcing.docs.filter(x => x.data().date >= fdate &&  x.data().date <= tdate).map((doc, index) => {
					if(allRecords[doc.data().orderId] !== undefined)
					{
						allRecords[doc.data().orderId] = doc.data().status == 'Delivered' ? allRecords[doc.data().orderId] + doc.data().quantity : 	allRecords[doc.data().orderId]
					}
					else{
						allRecords[doc.data().orderId]=0;
					}
				})
				let extTrips = {};
				trips.docs.filter(x => x.data()._id >= fdate &&  x.data()._id <= tdate).map((doc, index) => {
		
					if(!extTrips[doc.data().truckId]) extTrips[doc.data().truckId]=1;
					else extTrips[doc.data().truckId] = extTrips[doc.data().truckId] + 1;
					
		
					if(allRecords[doc.data().orderId] !== undefined)
					{
						allRecords[doc.data().orderId] = doc.data().status == 'Delivered' ? allRecords[doc.data().orderId] + doc.data().quantity : 	allRecords[doc.data().orderId]
					}
					else{
						allRecords[doc.data().orderId]=0;
					}
					if(index+1 == trips.docs.length && allTripsLoading){
						setAllTripsLoading(false);
						setAllTrips(allRecords);
						setExternalTrips(extTrips);
					}
				})
			}
		
			setFdate(fdate);
			setTdate(tdate);
			setOrderTrips(trps);
			setOrderOutsourced(outs);
			setRoadTalls(talls);
			setOrderFuel(fls);
			setOrderGarage(grg);
			setAllOrders(ords);

			// toMap.map((subscriber, index) => setTableData(index,subscriber,toMap.length));
			setLoaded(true);
			setVisible(false);
		} catch (errInfo) {}
	  };
	  
	return (
		<Card>
			{!loaded ? (<Flex alignItems="center" justifyContent="between" mobileFlex={false}>
				<Flex className="mb-1" mobileFlex={false}>
					<div className="mr-md-3">
						<Title level={4}>Generate Report</Title>
					</div>
				</Flex>
				<div>
					<Button onClick={() => setVisible(true)} size="small" type="primary" icon={<CloudDownloadOutlined />} block>Generate</Button>
				</div>
			</Flex>)
			:
			(<List systemData={systemData} 
				avatarColors={avatarColors}
				trucks={trucks}
				trucksLoading={trucksLoading}
				rentedTrucks={rentedTrucks}
				rentedTrucksLoading={rentedTrucksLoading}
				currencies={currencies}
				currenciesLoading={currenciesLoading}
				destinations={destinations}
				destinationsLoading={destinationsLoading}
				roadTalls={roadTalls} 
				allOrders={allOrders} 
				orderTrips={orderTrips} 
				allDestinationsLoading={allDestinationsLoading} 
				allDestinations={allDestinations} 
				allCurrencyLoading={allCurrencyLoading} 
				allCurrency={allCurrency}
				allTrucks={allTrucks}
				allTrips={allTrips}
				orderOutsourced={orderOutsourced}
				allBorders={allBorders}
				orderFuel={orderFuel}
				orderGarage={orderGarage}
				allCountries={allCountries}
				allCountriesLoading={allCountriesLoading}
				allRentedTrucks={allRentedTrucks}
				tripDataLoading={tripDataLoading}
				tripData={tripData}
				rounder={rounder} 
				tDate={tDate}
				fDate={fDate}
				setLoaded={setLoaded}
				setVisible={setVisible}/>
			)}
			<Modal
              title="New Report"
              visible={visible}
              onOk={handleOk}
              onCancel={handleCancel}
            >
				<Form layout="vertical" form={form}>
					<Row gutter={16}>
						<Col xs={24} sm={24} md={24}>
							<Form.Item name="range-picker" label="Pick Dates" {...rangeConfig}>
								<RangePicker className="w-100" disabledDate={disabledDate}/>
							</Form.Item>
						</Col>
					</Row>
				</Form>
            </Modal>
		</Card>
	)
}

export default Report
